.viv-calc {
  display: grid;
  grid-template-areas: 'left vertical-divider right' 'bottom bottom bottom';
  grid-template-columns: 1fr auto 1fr;
  column-gap: 55px;
  row-gap: 40px;
  margin-top: 50px;
}
.viv-calc__left {
  grid-area: left;
}
.viv-calc__right {
  grid-area: right;
}
.viv-calc__bottom {
  grid-area: bottom;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.viv-calc__bottom-line {
  position: absolute;
  top: 52%;
  transform: translateY(-48%);
  width: 100%;
  height: 2px;
  border-top: 1px solid #1074b9;
}
.viv-calc__vertical-divider {
  grid-area: vertical-divider;
  height: 100%;
  width: 2px;
  border-left: 1px dashed #1074b9;
}
.viv-calc__checkbox-row {
  display: flex;
  align-items: center;
  margin-bottom: 28px;
}
.viv-calc__checkbox-prepend-text {
  margin-right: 10px;
}
.viv-calc .input-with-label:not(:last-child) {
  margin-bottom: 25px;
}
.viv-calc label {
  font-size: 1.07142857rem;
}
.viv-calc .btn {
  padding-right: 45px;
  padding-left: 45px;
}
@media (max-width: 768px) {
  .viv-calc {
    display: grid;
    grid-template-areas: 'left' 'vertical-divider' 'right' 'bottom';
    grid-template-columns: auto;
    margin-top: 20px;
  }
  .viv-calc__vertical-divider {
    height: 2px;
    width: 100%;
    border-top: 1px dashed #1074b9;
    border-left: none;
  }
  .viv-calc__checkbox-row {
    margin-bottom: 15px;
  }
  .viv-calc .input-with-label:not(:last-child) {
    margin-bottom: 20px;
  }
}
.payment-widget-with-text {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
}
.payment-widget-with-text__left {
  display: flex;
  justify-content: center;
  align-items: center;
}
.payment-widget-with-text__left .payment-widget {
  width: 100%;
}
.payment-widget-with-text__right {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
}
.payment-widget-with-text__text {
  color: #243970;
  font-size: 1.07142857rem;
  font-weight: 400;
  line-height: 1.78571429rem;
}
@media (max-width: 768px) {
  .payment-widget-with-text {
    row-gap: 25px;
    grid-template-rows: 1fr auto;
    grid-template-columns: none;
  }
  .payment-widget-with-text__right {
    padding-left: 0;
  }
}
@media (max-width: 500px) {
  .payment-widget-with-text {
    row-gap: 20px;
  }
}
html.color-white .payment-widget-with-text__text {
